export const baseUrl = "https://crochetnest.com/";
const brandLogoUrl = "https://crochetnest.com/icons/icon-512x512.png";
const brandName = "Crochet Nest";

const offers = {
    "@type": "Offer",
    "availability" : "http://schema.org/InStock",
    "price": '',
    "priceCurrency": "USD"
};

export const productJsonLd = {
    "@context": "https://www.schema.org",
    "@type": "product",
    "brand": brandName,
    "logo": brandLogoUrl,
    "name": "",
    "category": "",
    "image": "",
    "description": "",
    "aggregateRating": {
        "@type": "aggregateRating",
        "ratingValue": "5",
        "reviewCount": "60"
    },
};

export const homeJsonLd = {
  "@context": "http://schema.org/",
  "@type": "Organization",
  "name": brandName,
  "logo": brandLogoUrl,
  "url": baseUrl,
  "description": "Discover the unique items that Crochet Nests creates. We sell the best crochet products.",
  "sameAs": ["https://www.instagram.com/_crochetnest/", "https://www.etsy.com/shop/LeBirb", "https://crochetnests.etsy.com"]
};

export const offerCatalogJsonLd = {
  "@context": "http://schema.org/",
  "@type": "OfferCatalog",
  "itemListElement": [],
}

function formatDate(date) {
  var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

  if (month.length < 2) 
      month = '0' + month;
  if (day.length < 2) 
      day = '0' + day;

  return [year, month, day].join('-');
}

export const createProductJsonLd = (product) => {
  const date = formatDate(new Date(new Date().setFullYear(new Date().getFullYear() + 1)));

  return {
    ...productJsonLd,
    name: product.title,
    category: 'crochet',
    image: product.images.length > 0 ? product.images : '',
    description: product.description,
    url: `${baseUrl}/product/${product.urlSuffix}/`,
    offers: {
      ...offers,
      price: product.price,
      priceValidUntil: date,
    }
  };
};

export const createCatalogJsonLd = (products) => {
  return {
    ...offerCatalogJsonLd,
    itemListElement: products.map((product, idx) => ({
      "@type": "ListItem",
      position: `${idx + 1}`,
      url: `${baseUrl}/product/${product.urlSuffix}/`,
      name: product.title,
    }))
  };
};