import { graphql } from "gatsby";

import React from "react";
import MainSectionWrapper from "../layout";
import CommonHead from "../layout/Head";
import Home from "../layout/pages/Home";
import JsonLd from "../layout/jsonLd";
import { createCatalogJsonLd, homeJsonLd } from "../seo/jsonLd";
import { parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse, } from '../graphql/products';

const PageTemplate = (props) => {
  const products = parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse(props.data);

  return (
    <>
      <MainSectionWrapper>
        <Home products={products} />
      </MainSectionWrapper>
      <JsonLd data={homeJsonLd} />
      <JsonLd data={createCatalogJsonLd(products)} />
    </>
  )
};

export function Head(props) {
  const data = {
    page: {
      name: "Crochet Nest Home",
    }
  };

  return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query PetitsAnimauxProducts {
    allGoogleSpreadsheetPetitsAnimauxProducts {
      edges {
        node {
          id
          title
          description
          price
          currencyCode
          prices
          urlSuffix
          image1Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image2Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image3Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image4Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image5Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image6Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image7Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image8Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image9Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
          image10Gatsby {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
        }
      }
    }
  }
`;

export default PageTemplate;