import React from "react";
import { Link as GatsyLink } from "gatsby";
import { Box, Stack, Heading } from '@chakra-ui/react';
import { GatsbyImage } from "gatsby-plugin-image";
import PriceTag from "../../components/PriceTag";

import "./styles.scss";

const ProductCard = (props) => {
  const { product } = props;
  return (
    <Box className="HomeProductCard">
      <GatsyLink to={`/product/${product.urlSuffix}`}>
        <Box className="HomeProductImageContainerWrapper">
          <Box className="HomeProductImageContainer">
            <GatsbyImage 
              className="HomeProductCardImage"
              image={product.gatsbyImages[0]}
              alt={product.title}
            />
          </Box>
        </Box>
        <Box>
          <Heading as='h3' size='sm' fontSize='16px' noOfLines={2}>{product.title}</Heading>
          <PriceTag price={product.price} />
        </Box>
      </GatsyLink>
    </Box>
  );
}

const Home = (props) => {
  const {products} = props;

  return (
    <div className="PetitsAnimauxHomeContainer" >
      
      <div className="PetitsAnimauxHomeHeader">
        <Heading as='h2' noOfLines={1}>All Items</Heading>
      </div>

      <Stack className="PetitsAnimauxHomeProductsContainer" 
            direction={['column', 'row']} 
            align="center"
            justify="space-between"
            spacing="16px"
            wrap="wrap" >
        {products.map((product) => {
          return (
            <ProductCard key={product.id} product={product} />
          );
        })}
      </Stack>
    </div>
  )
};

export default Home;